@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap);

* {
    margin: 0;
    border: 0;
    padding: 0;
    font-family: 'Source Sans Pro', sans-serif;
}

.page-header {
    width: 100vw;
    background-color: darkcyan;
    color: white;
    display: flex;
    justify-content: center;
    font-size: 20pt;

}

.content-header {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
#navbar-wrapper {
    background-color: black;
    display: flex;
    width: 100vw;
    justify-content: left;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
}

#navbar-wrapper li {
    padding: 1em;
    color: white;
    display: inline;
    font-size: 20pt;
}

#navbar-wrapper li:hover{
    background-color: darkblue;
    transition: .5s;
}

.navbar-brand {
    font-size: 30pt;
    color: aqua;
}

a:link {
    text-decoration: none;
    color: white;
}

a:visited {
    color: white;
    text-decoration: none;
}
.user-container {
    padding: .5em;
    display: flex;
    justify-content: space-between;
    background-color: lightgray;

}

.user-container {
    padding: 1.5em;

}

.user-container:hover {
    background-color: darkblue;
    color: white;
    transition: .5s;
}

.user-page-wrapper {
    display: flex;
    width: 100vw;
    flex-direction: column;
    justify-content: stretch;
}

.edit-user-form {
    transition: .5s;
}
.edit-form-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;


}

.edit-form-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    list-style-type: none;
}

.edit-form-content li {

}

.edit-form-content input{
}

.edit-form-content form {
    font-size: 20pt;
}

.edit-form-wrapper input {
    padding: 1em;
    font-size: 12pt;
}
